<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

   <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="rolelist" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']" v-for="(item1, i1) in scope.row.children" :key="item1.id">
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row, item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!--  for   -->
                <el-row :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']" v-for="(item2, i2) in item1.children" :key="item2.id">
                  <el-col :span="6">
                    <el-tag type="success" closable @close="removeRightById(scope.row, item2.id)">{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <!-- template  scope.row  -->
                    <el-tag type="warning" v-for="item3 in item2.children" :key="item3.id" closable @close="removeRightById(scope.row, item3.id)">{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>

            <!-- <pre>
              {{scope.row}}
            </pre> -->
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template v-slot="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit"></el-button>
            <el-button size="mini" type="danger" icon="el-icon-delete"></el-button>
            <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog
        title=""
        :visible.sync="setRightDialogVisible"
        width="50%">
      <el-tree
        :data="rightslist"
        :props="treeProps"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defKeys"
        @close="setRightDialogClosed"
        ref="treeRef">
      </el-tree>
      <span slot="footer" class="dialog-footer">
      <el-button @click="setRightDialogVisible = false"> </el-button>
      <el-button type="primary" @click="allotRights"> </el-button>
    </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Roles.vue",
  data() {
    return {
      // 
      rolelist: [],
      // 
      setRightDialogVisible: false,
      // 
      rightslist: [],
      // 
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // Id
      defKeys: [],
      // id
      roleId: ''
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    // 
    async getRolesList() {
      const {data: res} = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.rolelist = res.data
      // console.log(this.rolelist)
    },
    // Id
    async removeRightById(role, rightId) {
      // 
      const confirmResult = await this.$confirm(
          ', ?',
          '',
          {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete(
          `roles/${role.id}/rights/${rightId}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      //  getRolesList() 
      // this.getRolesList()
      role.children = res.data
    },
    // 
    async showSetRightDialog(role) {
      this.roleId = role.id
      // 
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      //  data 
      this.rightslist = res.data
      // console.log(this.rightslist)
      // Id
      this.getLeafKeys(role, this.defKeys)
      this.setRightDialogVisible = true
    },
    // id defKeys 
    getLeafKeys(node, arr) {
      //  node  children 
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item => this.getLeafKeys(item, arr))
    },
    // 
    setRightDialogClosed() {
      this.defKeys = []
    },
    // 
    async allotRights() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')

      const { data: res } = await this.$http.post(
          `roles/${this.roleId}/rights`,
          { rids: idStr }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      this.getRolesList()
      this.setRightDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px 30px;
}

.bdtop {
  border-top: 1px solid #eee;
}

.bdbottom {
  border-bottom: 1px solid #eee;
}

.vcenter {
  display: flex;
  align-items: center;
}
</style>
