<template>
  <div class="login_container">
    <div class="login_box">
      <!---->
      <div class="avatar_box">
        <img src="@/assets/logo.png" alt="">
      </div>
      <!---->
      <el-form ref="loginFormRef" :model="loginForm" :rules='loginFormRules' label-width="0px" class="login_form">
        <!---->
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>
        <!---->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" prefix-icon="iconfont icon-3702mima" type="password"></el-input>
        </el-form-item>
        <!---->
        <el-form-item class="btns">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="info" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      loginForm: {
        username: '',
        password: ''
      },
      // 
      loginFormRules: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        // 
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 
    resetLoginForm () {
      this.$refs.loginFormRef.resetFields();
    },
    // 
    login () {
      this.$refs['loginFormRef'].validate(async valid => {
        if (!valid) return
        //  Promise  async await (async )
        //const result = await this.$http.post("login", this.loginForm)
        //  async await result  Promise 
        //console.log(result)

        //  result ,1,3
        const { data: res } = await this.$http.post("login", this.loginForm)
        // console.log(res)
        if (res.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        // 1.  token sessionStorage 
        //   1.1 API
        //   1.2 token  token  sessionStorage 
        window.sessionStorage.setItem("token", res.data.token)
        // 2.  /home
        await this.$router.push('/home')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.login_container {
  background-color: #2b4b6b;
  height: 100%;
}

.login_box {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  transform: translate(-50%, -50%);
}

.avatar_box {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 130px;
  width: 130px;
  background-color: #fff;
  border: 1px solid #eee;
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 0 10px #ddd;
  img {
    width: 100%;
    height: 100%;
    background-color: #eee;
    border-radius: 50%;
  }
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.btns {
  display: flex;
  justify-content: flex-end;
}

</style>
